<template>
  <div style="background: #e7f0f9">
    <div
      style="
        background: #fff;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 60px;
      "
    >
      <div style="display: flex; justify-content: flex-start; align-items: center">
        <i id="b3" class="el-icon-s-unfold" @click="dianjichu"></i>
        <i id="b4" class="el-icon-s-fold" @click="dianjijin"></i>
        <div style="margin-left: 20px; font-weight: 550">{{ text }}</div>
      </div>
      <div class="b1">
        <el-menu
          :default-active="activeIndex2"
          class="el-menu-demo"
          mode="horizontal"
          @select="handleSelect"
          style="border-bottom: 0"
          background-color="#ffffff"
          text-color="#000"
          active-text-color="#1f87ff"
        >
          <el-menu-item index="1"></el-menu-item>
          <!-- <el-menu-item index="2"></el-menu-item> -->
          <el-menu-item index="3"></el-menu-item>
          <el-menu-item index="4"></el-menu-item>
          <el-menu-item index="7">API</el-menu-item>
          <el-menu-item v-if="account != ''" index="5"></el-menu-item>
        </el-menu>
      </div>

      <div style="display: flex; justify-content: flex-end; align-items: center">
        <div
          v-if="account == ''"
          style="cursor: pointer; width: 180px; text-align: right; margin-right: 25px"
          @click="denglu"
        >
          
        </div>
        <div
          v-if="account != ''"
          style="cursor: pointer; font-size: 14px; width: 180px; text-align: right"
        >
          {{ account }}
        </div>
        <div
          v-if="account != ''"
          style="margin-right: 20px; margin-left: 10px; position: relative"
        >
          <i v-if="!kuaiguan" class="el-icon-arrow-down" @click="kai"></i>
          <i v-if="kuaiguan" class="el-icon-arrow-up" @click="kai"></i>
          <div
            v-if="kuaiguan"
            style="
              font-size: 12px;
              position: absolute;
              width: 100px;
              background: #fff;
              top: 30px;
              left: -65px;
            "
          >
            <div
              style="margin: 5px; padding: 5px 0; text-align: center; cursor: pointer"
              @click="gaimima"
            >
              
            </div>
            <div
              style="margin: 5px; padding: 5px 0; text-align: center; cursor: pointer"
              @click="tuichu"
            >
              
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="b2">
      <el-menu
        :default-active="activeIndex2"
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
        style="border-bottom: 0"
        background-color="#fff"
        text-color="#000"
        active-text-color="#1f87ff"
      >
        <el-menu-item index="1"></el-menu-item>
        <!-- <el-menu-item index="2"></el-menu-item> -->
        <el-menu-item index="3"></el-menu-item>
        <el-menu-item index="4"></el-menu-item>
        <el-menu-item index="7">API</el-menu-item>
        <el-menu-item v-if="account != ''" index="5"></el-menu-item>
      </el-menu>
    </div>

    <el-dialog title="" :visible.sync="centerDialogVisible" width="30%" center>
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="pass">
          <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="checkPass">
          <el-input
            type="password"
            v-model="ruleForm.checkPass"
            autocomplete="off"
          ></el-input>
        </el-form-item>

        <el-form-item>
          <div style="text-align: right; width: 100%; margin-top: 20px">
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          </div>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import JSEncrypt from "jsencrypt";
import qs from "qs";

export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (this.ruleForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      text: '',
      currentPage: 0,
      activeIndex2: "",
      windowWidth: document.body.clientWidth,
      windowHeight: document.body.clientHeight,
      account: "",
      kuaiguan: false,
      centerDialogVisible: false,
      ruleForm: {
        pass: "",
        checkPass: "",
      },
      rules: {
        pass: [{validator: validatePass, trigger: "blur"}],
        checkPass: [{validator: validatePass2, trigger: "blur"}],
      },
    };
  },
  watch: {
    // 
    windowHeight(val) {
      this.windowHeight = val;
    },
    // 
    windowWidth(val) {
      if (val > 699) {
        var btn = document.querySelector(".b2");
        var b3 = document.querySelector("#b3");
        var b4 = document.querySelector("#b4");
        btn.style.display = "none";
        b3.style.display = "none";
        b4.style.display = "none";
      } else {
        var btn = document.querySelector(".b2");
        var b3 = document.querySelector("#b3");
        var b4 = document.querySelector("#b4");
        btn.style.display = "none";
        b3.style.display = "block";
        b4.style.display = "none";
      }

      // console.log('', val, this.windowHeight)
    },
  },
  mounted() {
    // <!--window.onresizemounted-->
    window.onresize = () => {
      return (() => {
        this.windowHeight = document.body.clientHeight; // 
        this.windowWidth = document.body.clientWidth; // 
      })();
    };
  },

  methods: {
    gaimima() {
      this.centerDialogVisible = true;
      this.ruleForm.pass = "";
      this.ruleForm.checkPass = "";
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.setmima();
        } else {
          return false;
        }
      });
    },
    setmima() {
      var encryptor = new JSEncrypt();
      encryptor.setPublicKey(
        ""
      ); // 
      var mimajiami = "DECRYPT:" + this.ruleForm.pass;
      var mima = encryptor.encrypt(mimajiami); // 
      var params = qs.stringify({
        password: mima,
      });

      this.$axios
        .post("/api/app/user/info/set/pwd", params, {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            Authorization: this.getCookie("nltoken"),
          },
        })
        .then((userData) => {
          if (userData.data.code == "200") {
            this.$message({
              message: userData.data.msg,
              type: "success",
            });
          } else {
            this.loading = false;
            this.$message.error(userData.data.msg);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    //
    tuichu() {
      this.delcookie("nltoken");
      this.delcookie("nlaccount");

      this.account = "";
    },
    kai() {
      this.kuaiguan = !this.kuaiguan;
    },
    genggai(num) {
      this.activeIndex2 = num;
    },
    dianjichu() {
      var btn = document.querySelector(".b2");
      var b3 = document.querySelector("#b3");
      var b4 = document.querySelector("#b4");
      btn.style.display = "block";
      b3.style.display = "none";
      b4.style.display = "block";
    },
    dianjijin() {
      var btn = document.querySelector(".b2");
      var b3 = document.querySelector("#b3");
      var b4 = document.querySelector("#b4");
      btn.style.display = "none";
      b3.style.display = "block";
      b4.style.display = "none";
    },

    handleSelect(key, keyPath) {
      // this.setcookie('daohangshu',key)
      if (key === '1') {
        this.$router.push({
          name: "index",
        });
      }
      if (key === '2') {
        this.$router.push({
          name: "orders",
        });
      }
      if (key === '3') {
        this.$router.push({
          name: "energy",
        });
      }
      if (key === '4') {
        this.$router.push({
          name: "record",
        });
      }
      if (key === '5') {
        this.$router.push({
          name: "my",
        });
      }
      if (key === '6') {
        this.$router.push({
          name: "appxz",
        });
      }
      if (key === '7') {
        this.$router.push({
          name: 'apiDoc'
        })
      }
    },
    denglu() {
      this.$router.push({
        name: "login",
      });
    },

    getzhanghu() {
      // var params = qs.stringify({
      //     'account': this.ruleForm.account,
      //     'code': this.ruleForm.code,
      // })

      this.$axios
        .post("/api/app/user/info", null, {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            Authorization: this.getCookie("nltoken"),
          },
        })
        .then((userData) => {
          if (userData.data.code == "200") {
            this.rechargeAddress = userData.data.data.rechargeAddress;
            this.setcookie("nlaccount", userData.data.data.account);
            this.account = userData.data.data.account;
          } else {
            this.loading = false;
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },

  created: function () {
    this.text = this.getCookie('nltext')
    if (this.getCookie('nlaccount') !== undefined && this.getCookie('nlaccount') !== '' && this.getCookie('nlaccount') !== null) {
      this.account = this.getCookie('nlaccount')
    } else {
      if (this.getCookie('nltoken') !== undefined && this.getCookie('nltoken') !== '' && this.getCookie('nltoken') !== null) {
        this.getzhanghu()
      }
    }
  },
  filters: {
    zhuangtai: function (data) {
      if (data === "1") {
        return "";
      }
      if (data === "2") {
        return "";
      }
      if (data === "3") {
        return "";
      }
    },
  },
};
</script>
<style scoped>
.gongyong {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 100000;
}

.b1 {
  display: block;
}

.b2 {
  width: 100%;
  display: none;

  /* height: calc(100vh - 60px); */
  background: rgb(84, 92, 100);
}

#b3 {
  font-size: 20px;
  padding: 10px;
  display: none;
}

#b4 {
  font-size: 20px;
  padding: 10px;
  display: none;
}

@media screen and (max-width: 699px) {
  .b1 {
    display: none;
  }

  .b2 {
    display: none;
    width: 100%;

    background: rgb(84, 92, 100);
  }

  #b3 {
    font-size: 20px;
    padding: 10px;
    display: block;
  }
}

/*   768px-992px  768px*/
@media screen and (min-width: 700px) and (max-width: 1200px) {
}

@media screen and (min-width: 1201px) {
}
</style>



